import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'userId'])
	},
		data() {
			return {
				user:'',
				goodsList:[
				],
				headerTop:0,
				//控制滑动效果
				typeClass:'goods',
				subState:'',
				theIndex:null,
				oldIndex:null,
				isStop:false
			}
		},
		onPageScroll(e){
			
		},
		//下拉刷新，需要page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
		    setTimeout(function () {
		        uni.stopPullDownRefresh();
		    }, 1000);
		},
		onLoad() {
			//兼容H5下排序栏位置










			let that = this;
			uni.getStorage({
				key:'userId',
				success: (res)=>{
						console.log(res.data);
						that.userId = res.data;
				},
			});
			console.log(that.userId);
			
			uni.request({
				url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/favorite/by/userId',
				method: 'POST',
				data: {
					id: that.userId
				},
				success: res => {
					console.log(res.data.data)
					that.goodsList = res.data.data.favoriteUserResponseList;
				},
				fail: () => {},
				complete: () => {}
			});
			
		},
		methods: {
			switchType(type){
				if(this.typeClass==type){
					return ;
				}
				uni.pageScrollTo({
					scrollTop:0,
					duration:0
				})
				this.typeClass = type;
				this.subState = this.typeClass==''?'':'show'+type;
				setTimeout(()=>{
					this.oldIndex = null;
					this.theIndex = null;
					this.subState = this.typeClass=='goods'?'':this.subState;
				},200)
			},
			
			//商品跳转
			toGoods(e){
				console.log(e.scenicSpotId)
				uni.request({
					url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/scenicSpot/by/id',
					method: 'GET',
					data: {
						id : e.scenicSpotId
					},
					success: res => {
							uni.navigateTo({
							url: '../../details/goods?id='+ e.scenicSpotId
				});
					},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//控制左滑删除效果-begin
			touchStart(index,event){
				//多点触控不触发
				if(event.touches.length>1){
					this.isStop = true;
					return ;
				}
				this.oldIndex = this.theIndex;
				this.theIndex = null;
				//初始坐标
				this.initXY = [event.touches[0].pageX,event.touches[0].pageY];
			},
			touchMove(index,event){
				//多点触控不触发
				if(event.touches.length>1){
					this.isStop = true;
					return ;
				}
				let moveX = event.touches[0].pageX - this.initXY[0];
				let moveY = event.touches[0].pageY - this.initXY[1];
				
				if(this.isStop||Math.abs(moveX)<5){
					return ;
				}
				if (Math.abs(moveY) > Math.abs(moveX)){
					// 竖向滑动-不触发左滑效果
					this.isStop = true;
					return;
				}
				
				if(moveX<0){
					this.theIndex = index;
					this.isStop = true;
				}else if(moveX>0){
					if(this.theIndex!=null&&this.oldIndex==this.theIndex){
						this.oldIndex = index;
						this.theIndex = null;
						this.isStop = true;
						setTimeout(()=>{
							this.oldIndex = null;
						},150)
					}
				}
			},
			
			touchEnd(index,$event){
				//解除禁止触发状态
				this.isStop = false;
			},
			
			//删除商品
			deleteCoupon(id,List){
				let len = List.length;
				for(let i=0;i<len;i++){
					if(id==List[i].id){
						console.log(List[i].id);
						uni.request({
							url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/delete/favorite',
							method: 'DELETE',
							data: {
								ids:[List[i].id]
							},
							success: res => {
								console.log(res);
								List.splice(i, 1);
							},
							fail: () => {},
							complete: () => {}
						});
						
						break;
					}
				}
				this.oldIndex = null;
				this.theIndex = null;
				
			},
			
			discard() {
				//丢弃
			}
			
			
		}
	}
